/* @tailwind base;
@tailwind components;
@tailwind utilities; */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Alata", sans-serif;
}

a {
  font-size: 14px;
  line-height: 1.7;
  color: #d1d5d0;
  margin: 0px;
  transition: all 0.5s ease-out;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
}

a:focus {
  outline: none !important;
}

a:hover {
  text-decoration: none;
  color: #dcf836;
  transition: all 0.5s linear;
}

/*---------------------------------------------*/

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
  transition: all 0.5s ease-out;
}

p {
  font-size: 14px;
  line-height: 1.7;
  color: #d1d5d0;
  margin: 0px;
  transition: all 0.5s ease-out;
}

ul,
li {
  margin: 0px;
  list-style-type: none;
}

span {
  color: #fff;
}

/*---------------------------------------------*/

input {
  outline: none;
  border: none;
}

textarea {
  outline: none;
  border: none;
}

textarea:focus,
input:focus {
  border-color: transparent !important;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-moz-placeholder {
  color: transparent;
}

input:focus::-moz-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder {
  color: transparent;
}

textarea:focus::-webkit-input-placeholder {
  color: transparent;
}

textarea:focus:-moz-placeholder {
  color: transparent;
}

textarea:focus::-moz-placeholder {
  color: transparent;
}

textarea:focus:-ms-input-placeholder {
  color: transparent;
}

input::-webkit-input-placeholder {
  color: #adadad;
}

input:-moz-placeholder {
  color: #adadad;
}

input::-moz-placeholder {
  color: #adadad;
}

input:-ms-input-placeholder {
  color: #adadad;
}

textarea::-webkit-input-placeholder {
  color: #adadad;
}

textarea:-moz-placeholder {
  color: #adadad;
}

textarea::-moz-placeholder {
  color: #adadad;
}

textarea:-ms-input-placeholder {
  color: #adadad;
}

/*---------------------------------------------*/

button {
  outline: none !important;
  border: none;
  background: transparent;
  color: #fff;
}

button:hover {
  cursor: pointer;
}

iframe {
  border: none !important;
}

.pink-color {
  color: #dd003f;
}

.bg-pink-color {
  background-color: #dd003f;
}

.bg-pink-color-hover:hover {
  background-color: #dd003f;
}

.bg-pink-color-focus:focus {
  background-color: #dd003f;
}

.yellow-color {
  color: #dcf836;
  transition: all 0.5s ease-out;
}

.yellow-color-hover:hover {
  transition: all 0.5s ease-out;
  color: #dcf836;
}

.bg-gray-blue-color {
  background-color: #0f2133;
}

.bg-dark-color {
  background-color: #020d18;
}

.bg-dark-blue-color {
  background-color: #06121e;
}

.bg-light-blue-color {
  background-color: #1c3d5d;
}

.bg-light-blue-color-hover:hover {
  background-color: #1c3d5d;
}

.bg-light-blue-color-focus:focus {
  background-color: #1c3d5d;
}

.title-color {
  transition: all 0.5s ease-out;
  color: #4280bf;
}

.title-color-hover:hover {
  transition: all 0.5s ease-out;
  color: #4280bf;
}

.admin-film .ant-table-column-sorters span {
  color: #000;
}

.movie-item .mv-img::after {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  content: "";
  box-shadow: inset -5px -50px 100px -15px #000000;
  -webkit-box-shadow: inset -5px -50px 100px -15px #000000;
  -moz-box-shadow: inset -5px -50px 100px -15px #000000;
  -o-box-shadow: inset -5px -50px 100px -15px #000000;
  transition: all 0.5s ease-out;
}

.movie-item:hover .mv-img {
  opacity: 0.25;
  transition: all 0.5s ease-out;
}

.movie-item .mv-img {
  transition: all 0.5s ease-in;
}

.movie-item:hover .hvr-inner {
  opacity: 1;
  transition: all 0.5s ease-out;
}

.list-movie .slick-slider .slick-dots {
  bottom: auto;
  top: -25px;
  text-align: end;
  right: 20px;
}

.list-movie .slick-slider .slick-dots li {
  margin-right: 1px;
}

.list-movie .slick-slider .slick-dots li button::before {
  opacity: 0.75;
  color: #fff;
  font-size: 11px;
}

.list-movie .slick-slider .slick-dots .slick-active button::before {
  color: #dcf836;
  opacity: 1;
}

.list-movie .slick-slider .slick-dots li button:hover::before {
  opacity: 1;
  color: #dcf836;
  transition: all 0.5s ease-out;
}

.trailers .trailer-left .slick-arrow {
  display: none !important;
}

.trailers .trailer-right .slick-arrow {
  left: 48%;
}

.trailers .trailer-right .slick-arrow.slick-prev {
  top: 1%;
}

.trailers .trailer-right .slick-arrow.slick-prev::before {
  content: "⮝";
  font-size: 30px;
}

.trailers .trailer-right .slick-arrow.slick-next {
  top: unset;
  bottom: 1%;
}

.trailers .trailer-right .slick-arrow.slick-next::before {
  content: "⮟";
  font-size: 30px;
}

.trailers .trailer-right .slick-arrow:hover::before {
  color: #dcf836;
  transition: all 0.5s ease-out;
}

.trailers .trailer-right .slick-arrow::before {
  transition: all 0.5s ease-out;
}

.trailers .trailer-right .trailer-film {
  display: flex !important;
  align-items: center;
}

.trailers .trailer-left iframe {
  width: 100%;
  height: 435px;
}

@media screen and (max-width: 768px) {
  .trailers .trailer-left iframe {
    height: 345px;
  }

  .trailers .trailer-film {
    padding: 10px;
  }

  .trailers .trailer-right .trailer-film {
    display: block !important;
  }

  .trailers .trailer-right .slick-arrow {
    left: 44%;
  }

  .trailers .trailer-right h3 {
    font-size: 12px;
  }

  .trailers .trailer-right img {
    width: 100%;
    height: 55px;
  }
}

@media screen and (max-width: 400px) {
  .trailers .trailer-left iframe {
    height: 300px;
  }

  .trailers .trailer-film {
    padding: 10px;
  }

  .trailers .trailer-right h3 {
    font-size: 5px;
  }
}

.carousel .slick-slider .slick-dots {
  bottom: 20px;
}

.carousel .slick-slider .slick-dots li button::before {
  opacity: 0.75;
  color: transparent;
  font-size: 12px;
  line-height: 12px;
  width: 12px;
  height: 12px;
  border: 1px solid white;
  border-radius: 50%;
}

.carousel .slick-slider .slick-dots .slick-active button::before {
  color: #dcf836;
  opacity: 1;
}

.carousel .slick-slider .slick-dots li button:hover::before {
  opacity: 1;
  color: #dcf836;
  transition: all 0.5s ease-out;
}

.carousel .subtitle {
  display: flex;
  position: absolute;
  top: 25%;
  left: 10%;
  font-size: 20px;
  color: #fff;
  letter-spacing: 1px;
}

.carousel .subtitle li + li {
  border-left: 2px solid #fff;
}

.carousel .subtitle li {
  padding: 0 10px;
}

.carousel .title {
  position: absolute;
  top: 30%;
  left: 10%;
  font-size: 70px;
  color: #fff;
  letter-spacing: 20px;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.carousel .slide_right {
  position: absolute;
  top: 50%;
  left: 10%;
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}

.carousel .slide_right .ant-modal-content .ant-modal-header {
  display: none;
}

.carousel .slide_right ul li + li {
  margin-left: 20px;
}

.carousel .chart-cirle {
  display: flex;
  position: absolute;
  bottom: 20%;
  right: 10%;
}

.carousel .chart-cirle .chart-circle-l {
  -webkit-animation-delay: 0.7s;
  animation-delay: 0.7s;
}

.carousel .chart-cirle .chart-circle-r {
  -webkit-animation-delay: 0.75s;
  animation-delay: 0.75s;
}

