    transparent
  );
}

.he-thong-rap .film:hover .ten-phim {
  color: #dcf836;
}

.he-thong-rap
  .ant-tabs-left
  > .ant-tabs-content-holder
  > .ant-tabs-content
  > .ant-tabs-tabpane {
